import textbookStore from '../store/textbook';
export default function useSetStorageWords(word, isCorrect, userId) {
    var wordInLocalStorage = localStorage.getItem("".concat(userId).concat(word.word));
    if (wordInLocalStorage) {
        var parseWordStats = JSON.parse(wordInLocalStorage);
        if (isCorrect) {
            parseWordStats.countWin += 1;
        }
        else {
            parseWordStats.countLose += 1;
        }
        if (parseWordStats.countWin === 3) {
            textbookStore.setLearnedWord(word);
        }
        localStorage.setItem("".concat(userId).concat(word.word), JSON.stringify(parseWordStats));
    }
    else {
        var newWordStats = {
            countWin: isCorrect ? 1 : 0,
            countLose: isCorrect ? 0 : 1,
        };
        localStorage.setItem("".concat(userId).concat(word.word), JSON.stringify(newWordStats));
    }
}
