import $api from '../axios';
export function getStats(userId) {
    return $api.get("/users/".concat(userId, "/statistics"));
}
export function setStats(userId, stats) {
    return $api.put("/users/".concat(userId, "/statistics"), stats);
}
export function getUserInfo(userId) {
    return $api.get("/users/".concat(userId));
}
export function setUserInfo(userId, user) {
    return $api.put("/users/".concat(userId), user);
}
