export var getGroupWords = function (group) {
    var endpoints = [];
    for (var i = 0; i <= 29; i += 1) {
        endpoints.push("/words?group=".concat(group, "&page=").concat(i));
    }
    return axios
        .all(endpoints.map(function (endpoint) {
        return $api
            .get(endpoint)
            .then(function (res) { return res.data; })
            .then(function (res) { return res.map(function (item) { return item.wordTranslate; }); });
    }))
        .then(function (res) { return res.flat(); });
};
