import axios from 'axios';
import $api from '../axios/index';
var getWords = function (group, page) {
    return $api.get("/words?group=".concat(group, "&page=").concat(page)).then(function (res) { return res.data; });
};
export default getWords;
export var getGroupWords = function (group) {
    var endpoints = [];
    for (var i = 0; i <= 29; i += 1) {
        endpoints.push("/words?group=".concat(group, "&page=").concat(i));
    }
    return axios.all(endpoints.map(function (endpoint) { return $api.get(endpoint).then(function (res) { return res.data; }); }));
};
